import Cookies from "js-cookie";

export const lotinKirilOtkazish = (matn) => {
  let loc = Cookies.get("language");
  if (loc != "kiril") {
    return matn;
  }

  var alifbo = {
    a: "",
    b: "",
    d: "",
    e: "",
    f: "",
    g: "",
    h: "",
    i: "",
    j: "",
    k: "",
    l: "",
    m: "",
    n: "",
    o: "",
    p: "",
    q: "",
    r: "",
    s: "",
    t: "",
    u: "",
    v: "",
    x: "",
    y: "",
    z: "",
    A: "",
    B: "",
    D: "",
    E: "",
    F: "",
    G: "",
    H: "",
    I: "",
    J: "",
    K: "",
    L: "",
    M: "",
    N: "",
    O: "",
    P: "",
    Q: "",
    R: "",
    S: "",
    T: "",
    U: "",
    V: "",
    X: "",
    Y: "",
    Z: "",
  };

  var kirilMatn = "";
  var i = 0;
  while (i < matn.length) {
    var harf = matn[i].toLowerCase();
    if (
      harf === "s" &&
      i < matn.length - 1 &&
      matn[i + 1].toLowerCase() === "h"
    ) {
      kirilMatn += "";
      i += 2;
    } else if (
      harf === "c" &&
      i < matn.length - 1 &&
      matn[i + 1].toLowerCase() === "h"
    ) {
      kirilMatn += "";
      i += 2;
    } else if (
      harf === "o" &&
      i < matn.length - 1 &&
      matn[i + 1].toLowerCase() === "'"
    ) {
      kirilMatn += "";
      i += 2;
    } else if (alifbo.hasOwnProperty(harf)) {
      kirilMatn += alifbo[harf];
      i++;
    } else {
      kirilMatn += harf;
      i++;
    }
  }

  return kirilMatn;
};
